html {
  font-size: 62.5%;
  @include rmin(1336) {
    font-size: 0.732vw; } }


body {
  min-width: 1024px;
  background: white;
  color: $color-text-main;
  @include f(p-regular, 14, 20);
  @include smooth-font; }

.out {
  overflow: hidden; }

.outer-wrap {
  padding: 0 $outer-padding; }

.inner-wrap {
  padding-left: $inner-padding; }

.justify {
  display: flex;
  justify-content: space-between; }

.section {
  position: relative;
  margin: rem(80) 0;
  &:first-of-type {
    margin-top: rem(40); }
  > p {
    margin: 1.5em 0; }
  &--sub {
    .section__number {
      color: $color-text-grey; } }
  &--1-2-1 {
    margin: rem(210) 0;
    .silouettes {
      bottom: 50%;
      margin-bottom: rem(-400); }
    .arrow-2 {
      margin-top: rem(190); } }
  &--1-2-2 {
    .arrow-3 {
      margin: rem(-80) 0 0 rem(120); } }
  &--1-3-1 {
    .arrow-4 {
      margin: rem(-30) 0 0 rem(50); } }

  &--2-2 {
    margin-top: rem(190); }
  &--2-2-1 {
    margin-bottom: rem(150);
    .silouettes {
      bottom: rem(-42); }
    .arrow-1 {
      z-index: 2;
      margin: rem(70) 0 0 rem(-70); } }

  &--3-2-1 {
    margin: rem(160) 0;
    .arrow-5 {
      margin: rem(50) 0 0; } }

  &--3-2-2 {
    .arrow-5 {
      margin-top: rem(-80); } }
  &--3-3-1 {
    margin-bottom: rem(160);
    .arrow-6 {
      margin: rem(100) 0 0 rem(200); } } }



.section__number {
  position: absolute;
  top: 0;
  left: -$inner-padding;
  @include f(m-semibold, 12, 18); }

.silouettes {
  position: absolute;
  bottom: rem(-42);
  right: 0;
  z-index: 1;
  @include responsiveImg('silouettes', 623, 727);
  width: 55%; }

.network-1 {
  margin: rem(-110) auto;
  @include responsiveImg('network1', 1073, 758, 'jpg'); }

.network-2 {
  margin: rem(-50) auto;
  @include responsiveImg('network2', 1073, 764); }

.network-3 {
  margin: auto;
  @include responsiveImg('network3', 682, 512); }

.network-4 {
  margin: rem(-50) auto 0;
  @include responsiveImg('network4', 1188, 594); }

.dndarea {
  margin: rem(-40) auto 0;
  @include responsiveImg('dndarea2', 1072, 721); }

%arrow {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 1; }

.arrow-1 {
  @extend %arrow;
  @include responsiveImg('arrow1', 212, 172); }

.arrow-2 {
  @extend %arrow;
  @include responsiveImg('arrow2', 112, 252); }

.arrow-3 {
  @extend %arrow;
  @include responsiveImg('arrow3', 38, 279); }

.arrow-4 {
  @extend %arrow;
  @include responsiveImg('arrow4', 194, 198); }

.arrow-5 {
  @extend %arrow;
  @include responsiveImg('arrow5', 111, 321); }

.arrow-6 {
  @extend %arrow;
  @include responsiveImg('arrow6', 59, 284); }

.hierarchy {
  margin: rem(-80) auto 0;
  @include responsiveImg('hierarchy', 1105, 565); }

.meeting {
  margin: rem(80) auto 0;
  @include responsiveImg('meeting', 916, 908, 'jpg'); }

.app-interface-1 {
  @include responsiveImg('app_interface1', 260, 524);
  width: rem(260); }

.app-interface-2 {
  @include responsiveImg('app_interface2', 260, 524);
  width: rem(260); }

.app-interface-3 {
  @include responsiveImg('app_interface3', 260, 524);
  width: rem(260); }

.app-interface-group {
  margin-top: rem(70);
  .step {
    margin-bottom: rem(70); } }

.people {
  margin: auto;
  @include responsiveImg('people', 990, 348, 'jpg'); }


