.heading {
  position: relative;
  .subtitle {
    margin-top: rem(10); } }

.heading__number {
  position: absolute;
  top: 0;
  left: -$inner-padding;
  @include f(m-semibold, 12, 18); }

.heading__regular-text {
  margin: 1em 0; }

.h1-like {
  @include f(m-bold, 36, 36, 250);
  text-transform: uppercase; }

.h2-like {
  @include f(m-bold, 36, 36);
  text-transform: capitalize; }

.h3-like {
  @include f(m-bold, 24, 24); }

.h4-like {
  @include f(m-bold, 18, 20); }

.subtitle {
  display: block;
  margin-top: em(10);
  @include f(m-regular, 14, 20);
  color: $color-text-grey; }
