$people-lines-height: 130, 205, 165, 110, 215, 165, 65, 120, 190;
$people-lines-left-pos: 68, 182, 282, 382, 500, 615, 710, 812, 925;
.people-group {
  position: relative;
  padding-top: percentage(250/990);
  max-width: rem(990);
  margin: rem(80) auto 0;
  .people {
    position: relative;
    z-index: 1; } }

.people-group__line {
  position: absolute;
  z-index: 0;
  bottom: percentage(250/600);
  left: 0;
  width: 1px;
  background: $color-line;
  height: rem(100);
  @for $i from 1 through 9 {
    &:nth-child(#{$i}) {
      left: percentage((nth($people-lines-left-pos, $i)) / 990);
      height: percentage((nth($people-lines-height, $i) + 100) / 600); } } }

.people-group__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: rem(150);
  margin: 0 0 rem(20) rem(-75);
  text-align: center;
  white-space: nowrap; }
