.bubbles-group {
  position: relative;
  margin: auto;
  width: 100%;
  max-width: em(1130);
  height: em(540);
  .network-3 {
    position: absolute;
    bottom: em(-45);
    left: 50%;
    margin-left: em(-90);
    transform: translate(-50%, 0); }
  .bubble {
    position: absolute;
    width: em(320);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    br {
      display: none; }
    &:first-child {
      margin: em(-240) 0 0 em(-40); }
    &:nth-child(2) {
      margin: em(-92) 0 0 em(325); }
    &:nth-child(3) {
      margin: em(143) 0 0 em(400); }
    &:nth-child(4) {
      margin: em(-40) 0 0 em(-427); }
    &:nth-child(5) {
      margin: em(208) 0 0 em(-410); } }
  @include r(1280) {
    font-size: rem(13); }
  @include r(1100) {
    font-size: rem(11); } }

.bubble {
  padding: em(15) em(18);
  border-radius: em(10);
  border: 1px solid #EEEEEE;
  color: $color-text-grey-darker;
  background: white; }

