.footer {
  background: $color-text-main;
  color: $color-text-grey-darker;
  padding-top: rem(60);
  padding-bottom: rem(40);
  font-size: rem(12);
  a:not([class]) {
    color: white;
    text-decoration: none; }
  .quote {
    margin-top: rem(25);
    font-size: inherit; }
  .quote__cite {
    margin-top: em(5); } }

.footer__row {
  display: flex;
  justify-content: space-between;
  margin-top: rem(70);
  &:first-child {
    margin-top: 0; } }

.footer__left {
  width: 50%; }

.footer__right {
  width: 50%;
  max-width: 430px; }

.footer__contacts {
  font-size: rem(14); }

.footer__nav {
  margin-bottom: rem(25);
  li {
    margin-bottom: rem(5); } }

.footer__copyright {
  display: inline-block;
  vertical-align: middle;
  margin-right: rem(25); }

.footer__title {
  color: white;
  @include f(m-regular);
  margin-bottom: em(5); }

.footer__design-by {
  color: $color-text-grey;
  @include f(m-regular, 12);
  a {
    margin-left: 0.5em;
    @include f(m-bold, 10, 10, 500);
    color: white;
    text-decoration: none;
    &:hover {
      text-decoration: none; } } }
