.list {
  margin-top: rem(50);
  &:first-child {
    margin-top: 0; }
  .list-item {
    margin-top: 1.5em;
    &:first-child {
      margin-top: 0; } }
  &--line-left {
    .list-item {
      &:before {
        right: 100%;
        margin-right: rem(55);
        left: -1000px;
        background: $color-text-main; } }
    .list-item__content {
      background: none;
      padding: 0; } }
  &--silouettes {
    .list-item {
      &:before {
        right: 300px; } } } }

.list-item {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0.7em;
    left: 0;
    right: -1000px;
    height: 1px;
    background: $color-line; }
  > * {
    background: white;
    box-shadow: em(25, 14) 0 0 0 white; }
  &--line-left {
    > * {
      background: none;
      box-shadow: none; }
    &:before {
      right: 100%;
      margin-right: rem(55);
      left: -1000px;
      background: $color-text-main; }
    .list-item__content {
      background: none;
      padding: 0; } } }

.list-item__content {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding-right: em(25, 14);
  background: white; }
